<template>
  <div class="asp">
    <div class="asp-nav"></div>
    <div class="asp-content">
      <div class="asp-form">
        <div class="asp-table">
          <div style="margin:19px 0 0 0">
            <el-form
              :model="ruleForm"
              :rules="rules"
              ref="ruleForm"
              label-width="100px"
              class="demo-ruleForm"
            >
              <el-form-item label="" prop="positionName">
                <el-input style="width:203px" placeholder="" v-model="ruleForm.positionName"></el-input>
              </el-form-item>
              <el-form-item label="" prop="nature">
                <el-select v-model="ruleForm.nature" placeholder="">
                  <el-option label="" value="1"></el-option>
                  <el-option label="" value="2"></el-option>
                  <el-option label="" value="3"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" prop="positionCatalog">
                <el-cascader
                  style="width:203px"
                  :options="positionCatalogList"
                  :props="propsTwo"
                  :show-all-levels="false"
                  v-model="ruleForm.positionCatalog"
                ></el-cascader>
              </el-form-item>
              <el-form-item label="" prop="salary">
                <el-select v-model="ruleForm.salary" placeholder="">
                  <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" prop="degree">
                <el-select v-model="ruleForm.degree" placeholder="">
                  <el-option
                    v-for="item in optionsDegree"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" prop="workYear">
                <el-select v-model="ruleForm.workYear" placeholder="">
                  <el-option
                    v-for="item in optionsWorkYear"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" prop="workCity">
                <el-cascader
                  style="width:203px"
                  :options="cityList"
                  :props="propsOne"
                  :show-all-levels="false"
                  v-model="ruleForm.workCity"
                ></el-cascader>
                <br />
                <el-input
                  style="width:500px;margin:20px 0 0 0"
                  placeholder=""
                  type="textarea"
                  v-model="ruleForm.desc"
                ></el-input>
              </el-form-item>
              <el-form-item label="" prop="positionCatalogDetail">
                <el-input
                  style="width:500px;"
                  type="textarea"
                  placeholder="300"
                  v-model="ruleForm.positionCatalogDetail"
                ></el-input>
              </el-form-item>
              <el-form-item label="" prop="JobSearch">
                <el-input
                  style="width:500px;"
                  type="textarea"
                  placeholder="300"
                  v-model="ruleForm.JobSearch"
                ></el-input>
              </el-form-item>
              <el-form-item label="" prop="skills">
                <el-input
                  style="width:500px;"
                  type="textarea"
                  placeholder=""
                  v-model="ruleForm.skills"
                ></el-input>
              </el-form-item>
              <el-form-item label="HR" prop="HR">
                <el-input style="width:203px" v-model="ruleForm.HR"></el-input>
              </el-form-item>
              <el-form-item label="" prop="email">
                <el-input style="width:203px" v-model="ruleForm.email"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
                <el-button @click="resetForm('ruleForm')"></el-button>
              </el-form-item>
            </el-form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import positionCatalog from "../../assets/positionCatalog.json";
import city from "../../assets/city.json";
export default {
  data() {
    return {
      ruleForm: {
        positionName: "",
        nature: "",
        positionCatalog: "",
        salary: "",
        degree: "",
        workYear: "",
        workCity: "",
        desc:'',
        positionCatalogDetail: "",
        JobSearch: "",
        skills: "",
        HR: "",
        email: ""
      },
      propsTwo: {
        value: "code",
        label: "tag",
        children: "children",
        emitPath: false
      },
      propsOne: {
        value: "code",
        label: "tag",
        children: "children",
        emitPath: false
      },
      options: [
        {
          value: "0",
          label: "1K"
        },
        {
          value: "1",
          label: "1K-2K"
        },
        {
          value: "2",
          label: "2K-4K"
        },
        {
          value: "3",
          label: "4K-6K"
        },
        {
          value: "4",
          label: "6K-8K"
        },
        {
          value: "5",
          label: "6K-8K"
        },
        {
          value: "6",
          label: "8K-10K"
        },
        {
          value: "7",
          label: "10K-15K"
        },
        {
          value: "8",
          label: "15K-25K"
        },
        {
          value: "9",
          label: "25K-35K"
        },
        {
          value: "10",
          label: "35K"
        }
      ],
      optionsDegree: [
        {
          value: "0",
          label: ""
        },
        {
          value: "1",
          label: "/"
        },
        {
          value: "2",
          label: ""
        },
        {
          value: "3",
          label: ""
        },
        {
          value: "4",
          label: ""
        },
        {
          value: "5",
          label: ""
        },
        {
          value: "6",
          label: ""
        }
      ],
      optionsWorkYear: [
        {
          value: "0",
          label: ""
        },
        {
          value: "1",
          label: "1-3"
        },
        {
          value: "2",
          label: "3-5"
        },
        {
          value: "3",
          label: "5-10"
        },
        {
          value: "4",
          label: "10"
        }
      ],
      positionCatalogList: [],
      cityList: [],
      rules: {
        positionName: [
          { required: true, message: "", trigger: "blur" }
          //   { min: 3, max: 5, message: " 3  5 ", trigger: "blur" }
        ],
        nature: [
          { required: true, message: "", trigger: "change" }
        ],
        positionCatalog: [
          { required: true, message: "", trigger: "change" }
        ],
        salary: [
          { required: true, message: "", trigger: "change" }
        ],
        degree: [
          { required: true, message: "", trigger: "change" }
        ],
        workYear: [
          { required: true, message: "", trigger: "change" }
        ],
        workCity: [
          { required: true, message: "", trigger: "change" }
        ],
        positionCatalogDetail: [
          { required: true, message: "", trigger: "change" },
          {
            min: 0,
            max: 300,
            message: " 0  300 ",
            trigger: "blur"
          }
        ],
        JobSearch: [
          { required: true, message: "", trigger: "change" },
          {
            min: 0,
            max: 300,
            message: " 0  300 ",
            trigger: "blur"
          }
        ],
        HR: [{ required: true, message: "HR", trigger: "change" }],
        email: [
          { required: true, message: "", trigger: "change" }
        ]
      }
    };
  },
  methods: {
    //
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //
    resetForm(formName) {
      this.ruleForm.desc = ''
      this.$refs[formName].resetFields();
    },
    // //
    // submitForm() {
    //   this.$http
    //     .post("", {})
    //     .then(res => {
    //       if (res.data.code == 200) {
    //       } else {
    //       }
    //     })
    //     .catch(error => {
    //       this.$message(error.response.data.message);
    //     });
    // }
  },
  mounted: function() {},
  updated: function() {},
  created() {
    this.positionCatalogList = positionCatalog.data;
    this.cityList = city.data;
  }
};
</script>
<style lang="scss">
.asp {
  width: 100%;
  background: #f8f8f8;
  border: 1px solid #f8f8f8;
}
.asp-nav {
  margin: 40px 0 0 20px;
  font-size: 18px;
  color: #2a2a2a;
  text-align: left;
}
.asp-content {
  width: 100%;
  margin: 20px 0 20px 0;
  background: #f8f8f8;
}
.asp-form {
  width: 98%;
  margin: 10px 0 0 1%;
  border: 1px solid #f8f8f8;
}
.asp-table {
  width: 100%;
  border: 1px solid #f8f8f8;
  background: #ffffff;
  margin: 10px 0 0 0;
}
</style>


