<template>
  <div class="asp">
    <div class="asp-nav"></div>
    <div class="asp-content">
      <el-dialog
        title
        :show-close="false"
        :append-to-body="true"
        :visible.sync="dialogVisible"
        width="25%"
        :before-close="handleClose"
      >
        <div>
          <div class="loading">
            <i style="font-size:60px;color:#ffbf27;" class="el-icon-warning-outline"></i>
          </div>
          <div class="loading-text">!</div>
          <div style="text-align:center">
            <el-button
              style="width:93px;height:33px;line-height:0px"
              @click="keep()"
              type="primary"
            ></el-button>
          </div>
        </div>
      </el-dialog>
      <el-dialog
        title
        :show-close="false"
        :append-to-body="true"
        :visible.sync="dialogVisibleDelete"
        width="25%"
        :before-close="handleClose"
      >
        <div>
          <div style="margin:-30px 0 0 0" class="loading-text"></div>
          <div style="text-align:center;margin:50px 0 30px 0">
            <span slot="footer" class="dialog-footer">
              <el-button
                style="width:93px;height:33px;line-height:0px;margin:0 40px 0 0"
                @click="dialogVisibleDelete = false"
              > </el-button>
              <el-button
                style="width:93px;height:33px;line-height:0px"
                type="primary"
                @click="dialogVisibleDelete = false"
              > </el-button>
            </span>
          </div>
        </div>
      </el-dialog>
      <el-dialog
        title
        :show-close="false"
        :append-to-body="true"
        :visible.sync="dialogVisibleOut"
        width="25%"
        :before-close="handleClose"
      >
        <div>
          <div style="margin:-30px 0 0 0" class="loading-text"></div>
          <div style="text-align:center;margin:50px 0 30px 0">
            <span slot="footer" class="dialog-footer">
              <el-button
                style="width:93px;height:33px;line-height:0px;margin:0 40px 0 0"
                @click="dialogVisibleOut = false"
              > </el-button>
              <el-button
                style="width:93px;height:33px;line-height:0px"
                type="primary"
                @click="dialogVisibleOut = false"
              > </el-button>
            </span>
          </div>
        </div>
      </el-dialog>
      <div class="asp-form">
        <el-form
          :inline="true"
          :model="formInline"
          label-width="130px"
          ref="formInline"
          class="demo-form-inline"
          style="background:#ffffff;"
        >
          <div style="height:20px"></div>
          <el-form-item label="">
            <el-input v-model="formInline.positionName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="formInline.state" placeholder="">
              <el-option label="" value="shanghai"></el-option>
              <el-option label="" value="beijing"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="formInline.popName" placeholder="">
              <el-option label="" value="shanghai"></el-option>
              <el-option label="" value="beijing"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item style="margin:0 90px 0 0;display: flex;justify-content:flex-end">
            <el-button style="margin:0 0 30px 0" @click="resetForm()"></el-button>
            <el-button type="primary" @click="submitForm()"></el-button>
          </el-form-item>
        </el-form>
        <div class="asp-table">
          <div style="margin:30px 0 30px 30px">
            <el-button type="primary" @click="toggleSelection()"></el-button>
          </div>
          <div>
            <el-table :data="tableData" tooltip-effect="dark" style="width:1084px;margin:0 auto">
              <!-- <el-table-column type="selection" width="60"></el-table-column> -->
              <!-- <el-table-column label="" width="100">
                <template slot-scope="scope">{{ scope.row.date }}</template>
              </el-table-column>-->
              <!-- <el-table-column prop="name" label=""></el-table-column> -->
              <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
              <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
              <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
              <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
              <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
              <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
              <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
              <el-table-column label="" width="180">
                <template slot-scope="scope">
                  <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
                  <el-button
                    style="color:#FF4F4E"
                    type="text"
                    size="small"
                    @click="deletepositon()"
                  ></el-button>
                  <el-button style="color:#FF4F4E" type="text" size="small"></el-button>
                </template>
              </el-table-column>
            </el-table>
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="currentPage"
              :page-sizes="[100, 200, 300, 400]"
              :page-size="100"
              layout="total, sizes, prev, pager, next, jumper"
              :total="400"
            ></el-pagination>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      dialogVisible: false,
      dialogVisibleDelete: false,
      dialogVisibleOut: true,
      formInline: {
        positionName: "",
        state: "",
        popName: ""
      },
      currentPage: 4,
      tableData: [
        {
          date: "2016-05-03",
          name: "",
          address: " 1518 "
        },
        {
          date: "2016-05-02",
          name: "",
          address: " 1518 "
        },
        {
          date: "2016-05-04",
          name: "",
          address: " 1518 "
        },
        {
          date: "2016-05-01",
          name: "",
          address: " 1518 "
        },
        {
          date: "2016-05-08",
          name: "",
          address: " 1518 "
        },
        {
          date: "2016-05-06",
          name: "",
          address: " 1518 "
        },
        {
          date: "2016-05-07",
          name: "",
          address: " 1518 "
        }
      ],
      multipleSelection: []
    };
  },
  methods: {
    // 
    keep() {
      this.dialogVisible = false;
    },
    deletepositon() {
      this.dialogVisible = true;
    },
    //
    resetForm() {
      this.formInline.UserName = "";
      this.formInline.Phone = "";
      this.formInline.Record = "";
    },
    //
    submitForm() {
      this.$http
        .post("", {})
        .then(res => {
          if (res.data.code == 200) {
          } else {
          }
        })
        .catch(error => {
          this.$message(error.response.data.message);
        });
    },
    toggleSelection() {
      this.$router.push({ path: "/AddPosition" });
      // if (rows) {
      //   rows.forEach(row => {
      //     alert(1121)
      //     this.$refs.multipleTable.toggleAllSelection(row);
      //   });
      // } else {
      //   alert(11211)
      //   this.$refs.multipleTable.clearSelection();
      // }
    },
    handleClick(tab, event) {
      console.log(tab, event);
      this.$router.push({ path: "/editPosition" });
    },
    handleSizeChange(val) {
      console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
    }
  },
  mounted: function() {},
  updated: function() {}
};
</script>
<style lang="scss">
.asp {
  width: 100%;
  background: #f8f8f8;
  border: 1px solid #f8f8f8;
}
.asp-nav {
  margin: 40px 0 0 20px;
  font-size: 18px;
  color: #2a2a2a;
  text-align: left;
}
.asp-content {
  width: 100%;
  margin: 20px 0 20px 0;
  background: #f8f8f8;
}
.asp-form {
  width: 98%;
  margin: 10px 0 0 1%;
  border: 1px solid #f8f8f8;
}
.el-form-item {
  padding: 10px 0 0 20px;
}
.el-select {
  width: 203px;
}
.asp-table {
  width: 100%;
  border: 1px solid #f8f8f8;
  background: #ffffff;
  margin: 10px 0 0 0;
  .el-table th {
    background: #f0f0f0;
  }
}
.el-pagination {
  margin: 20px 0 0 0;
  padding: 0 0 20px 0;
  text-align: center;
}
.loading-text {
  font-size: 22px;
  color: #222222;
  text-align: center;
  margin: 30px 0 30px 0;
  font-weight: bold;
}
</style>


